.total{
  display: flex;
  flex-direction: column;
  margin-right: auto;
  width: 32%;

  @media (max-width: 1100px) {
    width: auto;
    justify-content: space-between;
    margin: 0;
  }

  label {
    font-family: Gilroy, sans-serif;
    font-size: 16px;
    line-height: 20px;
    color: #575757;
    margin-bottom: 8px;

    @media (max-width: 768px) {
      font-size: 14px;
      line-height: 17px;
    }
  }

  span {
    font-family: Nekst-Black, sans-serif;
    font-weight: 900;
    font-size: 54px;
    line-height: 54px;
    color: #333333;

    @media (max-width: 768px) {
      font-size: 22px;
      line-height: 20px;
    }
  }
}
