.paramsContainer {
  display: flex;
  justify-content: space-between;
  margin: 32px 0 44px;

  @media (max-width: 1100px) {
    flex-direction: column;

    div + div {
      margin-top: 32px;
    }
  }

  > div {
    width: 32%;

    @media (max-width: 1100px) {
      width: 100%;
    }
  }

  .blockPrice > div:nth-child(2) {
    position: relative;

    &::after {
      content: 'â‚½';
      font-family: Nekst-Black, sans-serif;
      font-style: normal;
      font-weight: 900;
      font-size: 30px;
      line-height: 36px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 24px;
      z-index: 2;
    }
  }


}
