.percentContainer {
  display: flex;
  flex-direction: column;

  label {
    margin-bottom: 24px;
    font-family: Gilroy, sans-serif;
    font-size: 16px;
    line-height: 20px;
    color: #575757;

    @media (max-width: 768px) {
      font-size: 14px;
      line-height: 17px;
    }
  }

  .percentBlock {
    position: relative;

    .sliderBlock {
      position: absolute;
      bottom: -3px;
      width: 90%;
      left: 50%;
      transform: translateX(-50%);
    }

    .percentInput {
      position: relative;

      .littleBlock {
        position: absolute;
        width: 69px;
        height: 54px;
        right: 7px;
        top: 50%;
        transform: translateY(-50%);
        background: #EBEBEC;
        border-radius: 16px;
        display: flex;
        justify-content: center;
        align-items: center;


        span {
          font-family: Nekst-Black, sans-serif;
          font-weight: 900;
          font-size: 20px;
          line-height: 120%;
          color: #575757;
          padding: 5px;
          text-align: center;

          &:hover {
            background: #F3F3F4;
          }

          &:focus {
            border: 2px solid #F3F3F4;
            background: #fff;
          }

          &:disabled {
            background-color: #F3F3F4;
            opacity: 0.5;
            cursor: default;
          }
        }
      }
    }

  }


  input[type='text'] {
    width: 100%;
    height: 70px;
    background: #F3F3F4;
    color: #575757;
    border-radius: 16px;
    border: none;
    padding: 16px 50px 16px 24px;
    font-family: Nekst-Black, sans-serif;
    font-weight: 900;
    font-size: 30px;
    line-height: 36px;
    outline: #F3F3F4;

    @media (max-width: 768px) {
      font-size: 22px;
      line-height: 20px;
    }

    &:hover {
      background: #F3F3F4;
    }

    &:focus {
      border: 2px solid #F3F3F4;
      background: #fff;
    }

    &:disabled {
      background-color: #F3F3F4;
      opacity: 0.5;
      cursor: default;
    }
  }
}
