.container {
  display: flex;
  justify-content: space-between;
  position:relative;

  @media (max-width: 1100px) {
    padding-bottom: 100px;

    .buttonApplication {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 345px;

      @media (max-width: 550px) {
        position: static;
        width: 100%;
      }
    }
  }

  @media (max-width: 550px) {
    flex-direction: column;
    padding: 0;

    div + div {
      margin-top: 30px;
    }
  }

}

> div {
  width: 32%;

  @media (max-width: 1100px) {
    width: 100%;
  }

}
