@font-face {
  font-family: 'Gilroy-Regular';
  font-style: normal;
  font-weight: 400;
  src: local('Gilroy-Regular.svg#Gilroy-Regular'), url('assets/fonts/Gilroy/Gilroy-Regular.svg') format('svg'),
  local('Gilroy-Regular.ttf'), url('assets/fonts/Gilroy/Gilroy-Regular.ttf') format('ttf'),
  local('Gilroy-Regular.woff'), url('assets/fonts/Gilroy/Gilroy-Regular.woff') format('woff'),
  local('Gilroy-Regular.woff2'), url('assets/fonts/Gilroy/Gilroy-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'Nekst-Black';
  font-style: normal;
  font-weight: 900;
  src: local('Nekst-Black.svg#Nekst-Black'), url('assets/fonts/Nekst-Black/Nekst-Black.svg') format('svg'),
  local('Nekst-Black.ttf'), url('assets/fonts/Nekst-Black/Nekst-Black.ttf') format('ttf'),
  local('Nekst-Black.woff'), url('assets/fonts/Nekst-Black/Nekst-Black.woff') format('woff'),
  local('Nekst-Black.woff2'), url('assets/fonts/Nekst-Black/Nekst-Black.woff2') format('woff2');
}

body {
  margin: 0;
  font-family: Nekst-Black, sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
p,
ul[class],
ol[class],
li,
span {
  margin: 0;
}

a {
  text-decoration: none;
}

button {
  outline: none;
  border: none;
  appearance: none;
  color: inherit;
  font-family: inherit;
  background: none;
  font-size: inherit;
}

