.container{
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin:0 auto;
  height: 100vh;
  padding: 200px 48px;

  @media (max-width: 550px) {
    padding: 200px 20px;
  }

  .title {
    max-width: 753px;
    font-size: 54px;
    line-height: 54px;

    @media (max-width: 768px) {
      font-size: 34px;
      line-height:34px;
    }
  }
}
