.button {
  padding: 8px 16px 12px;
  width: 426px;
  height: 68px;
  background: #FF9514;
  border-radius: 40px;
  font-family: Nekst-Black, sans-serif;
  font-weight: 900;
  font-size: 30px;
  line-height: 36px;
  color: #FFFFFF;
  display: block;
  cursor: pointer;

  @media (max-width: 1400px) {
    width: 380px;
  }

  @media (max-width: 1300px) {
    width: 100%;
  }


  &:disabled {
    img {
      position: relative;
      display: block;
      animation: preloader-rotate 2s infinite ease-in-out;
      margin: 0 auto;
    }
  }

  @keyframes preloader-rotate {
    100% {
      transform: rotate(360deg);
    }
  }

  @media (max-width: 768px) {
    font-size: 22px;
    line-height: 20px;
  }

  &:hover {
    background: #111111;
  }

  &:active {
    background: #575757;
  }

  &:disabled {
    background: rgba(255, 149, 20, 0.4);
  }
}
