.sliderContainer {
  input[type=range] {
    -webkit-appearance: none;
    width: 100%;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    animate: 0.2s;
    background: #FF9514;
    border-radius: 2px;
  }
  input[type=range]::-webkit-slider-thumb {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: #FF9514;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -7px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #FF9514;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    animate: 0.2s;
    background: #FF9514;
    border-radius: 2px;
  }
  input[type=range]::-moz-range-thumb {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: #FF9514;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    border-width: 2px 0;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #FF9514;
    border-radius: 4px;
  }
  input[type=range]::-ms-fill-upper {
    background: #FF9514;
    border-radius: 4px;
  }
  input[type=range]::-ms-thumb {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: #FF9514;
    cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #FF9514;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #FF9514;
  }

  input[type="range"] {
    &:disabled {
      background-color: #F3F3F4;
      opacity: 0.5;
      cursor: default;
    }
  }
}
